import styled from 'styled-components';

export const DetailWrapper = styled("div")`
    overflow: hidden;;
    width: 700px;
    margin: 20px auto;
    padding-bottom: 100px;
`;


export const Header = styled("div")`
    margin: 50px 0 20px 0;
    line-height: 34px;
    font-size: 34px;
    color: #333;
    font-weight: bold;
`;

export const Content = styled("div")`
     color: #333;
     h3 {
       font-weight: bold;
     }
     p {
       font-size: 14px;
     }
`;
